.intro {
    display: flex;
    height: 100vh;
}

.intro-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro-left-wrapper {
    padding: 50px;
    height: 62.5%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.intro-intro {
    font-size: 30px;
    font-weight: 300;
}

.intro-name {
    font-size: 60px;
    font-weight: 400;
}

.intro-title {
    height: 50px;
    overflow: hidden;
}

.intro-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    33.3% {
        transform: translateY(-50px);
    }
    66.7% {
        transform: translateY(-100px);
    }
    100% {
        transform: translateY(-150px);
    }
}

.intro-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: 300;
    color: #555D52;
    display: flex;
    align-items: center;
}

.intro-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

.intro-desc {
    font-size: 25px;
    font-weight: 300;
}

.intro-right {
    flex: 1;
    position: relative;
}

.intro-bg {
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 58% 54%);
    background-image: url(../../img/mountain-pic.jpg);
    background-position: center;
    background-size: cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.intro-img {
    max-width: 85%;
    height: 35%;
    position: absolute;
    top: 35%;
    right: 30%;
}

@media screen and (max-width: 992px) {
    .intro-img {
        height: 30%;
        top: 35.5%;
        right: 30%;
    }
    .intro-desc {
        font-size: 20px;
    }
}

@media screen and (max-width: 768px) {
    .intro {
        flex-direction: column;
    }

    .intro-left-wrapper {
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }

    .intro-title-item {
        justify-content: center;
    }

    .intro-desc {
        display: none;
    }
    .intro-img {
        height: 30vh;
        width: 33vw;
        right: 33%;
        top: 25%;
    }
}

@media screen and (max-width: 624px) {
    .intro-img {
        height: 28vh;
        width: 41.5vw;
        right: 30%;
        top: 27%;
    }
}

@media screen and (max-width: 480px) {
    .intro {
        flex-direction: column;
    }

    .intro-left-wrapper {
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }

    .intro-desc {
        display: none;
    }
    .intro-img {
        height: 25vh;
        width: 47.5vw;
        left: 25%;
        top: 29%;
    }
}
